.spa_card {
  margin: 1vw;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}
.spa_calendar {
  width: fit-content;
  padding: 1vw;
  background-color: #ffffff;
  border: 0.5px solid rgba(0, 0, 0, 0.12);
  border-radius: 8px;
}
.spac_inner {
  display: flex;
  gap: 1vw;
  justify-content: center;
  border-top: 0.4px solid rgb(233, 222, 222);
  padding-top: 1vw;
}
.shades {
  border-radius: 6px;
  width: 5vw;
  height: 2vw;
  display: grid;
  place-items: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  color: var(--text-color);
  background-color: var(--bg-color);
}
.spac_other {
  width: 55%;
}
.spac_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.spac_container_inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 0.6rem;
  cursor: pointer;
}
.spac_container_inner > p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 17px;
  color: #121212;
  margin: 0;
}
.spac_container_inner > img {
  width: auto;
  height: 1.5rem;
}
.spac_count {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 1rem;
  margin-top: 1rem;
  border-radius: 6px;
  background-color: var(--bg-sa-color);
}
.spac_count > p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 17px;
  color: #121212;
  margin: 0;
}
